const messagesReducer = (state = initialState, action) => {

    switch (action.type) {
        case SEND_MESSAGE:
            return {
                ...state,
                dialogItemsData: state.dialogItemsData.map((i) => {
                    return i.userId === action.id
                        ? { ...i, messages: [...i.messages, { message: action.message, id: i.messages.length + 1, right: true, date: action.date, timeFormat: action.timeFormat }] }
                        : { ...i }
                }).sort((a, b) => { return b.messages[b.messages.length - 1].date - a.messages[a.messages.length - 1].date })
            }
        case SET_DIALOG_ID:
            return {
                ...state,
                currentDialogId: action.id
            }
        case SET_NEW_DIALOG:
            return {
                ...state,
                dialogItemsData: [...state.dialogItemsData, {
                    name: action.name, userId: action.userId, avatar: action.photo || avatar,
                    messages: [{ message: action.message, id: 1, right: true, date: action.date, timeFormat: action.timeFormat }]
                }].sort((a, b) => { return b.messages[b.messages.length - 1].date - a.messages[a.messages.length - 1].date }),
                currentDialogId: action.userId
            }
        case SEARCH_DIALOG:
            return {
                ...state,
                dialogsFromSearch: state.dialogItemsData.filter(i=>i.name.toLowerCase().includes(action.word.toLowerCase()))
            }
        case IS_SEARCH_MODE:
            return{
                ...state,
                isSearchMode: action.isMode
            }
        default: return state;
    }

}

export const sendMessage = (message, id) => ({ type: SEND_MESSAGE, message, id, date: Date.now(),  timeFormat: formatTime(new Date())  });
export const setDialogID = (id) => ({ type: SET_DIALOG_ID, id });
export const newDialog = (message, userId, photo, name) => ({ type: SET_NEW_DIALOG, userId, photo, name, message, date: Date.now(), timeFormat: formatTime(new Date()) })
export const messageFromPopUp = (data, user) => (dispatch, getState) => {

    const userId = user.id || user.userId;
    const photo = user.photos.small;
    const name = user.name || user.fullName;

    if (getState().Messages.dialogItemsData.find(i => i.userId === userId)) {

        dispatch(sendMessage(data.messagePopup, userId))
    }
    else {
        dispatch(newDialog(data.messagePopup, userId, photo, name))
    }
}
export const search = (word) => ({type: SEARCH_DIALOG, word})
export const setSearchMode = (isMode) => ({type: IS_SEARCH_MODE, isMode})
export default messagesReducer;